import { Component, OnInit } from '@angular/core';
import { ProductDirectoryBean } from './bean/product-directory.bean';
import { SelectItem } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router';

@Component({
  selector: 'app-product-directory',
  templateUrl: './product-directory.component.html',
  styleUrls: ['./product-directory.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * //产品管理-业务目录管理
 */
export class ProductDirectoryComponent implements OnInit {
  orgTreeDisplayG: boolean = false;
  first: number = 0;//分页控制 
  tabMesg: any = tableMessage;//列表无数据
  CatalogType: any = [];
  code: any;
  // bean
  ProductDirectoryBean: ProductDirectoryBean = new ProductDirectoryBean();
  //头部标题
  all_title: string = '业务目录配置';
  //修改的参数
  updateValue: any = [];
  //列表
  cars_2: any;
  arr_2: any;
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';
  //模态框
  showModel: string;

  display: boolean = false;
  //模态框的标题
  headerTitle: string;
  //归属部门
  belongDepart: string;
  ownerOrgId: string;
  departOrStaffPost: string;
  constructor(
    public commfunc: Commfunc,
    public router: Router,
    public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService) {
    this.codeValues() //调用方法，获取全部码值
    this.CatalogType = this.code['CatalogType'];
  }

  ngOnInit() {
    this.first = 0;
    this.queryClick();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  // 重置
  resetClick() {
    this.ProductDirectoryBean = new ProductDirectoryBean();
    this.departOrStaffPost = '';
  }
  //查询始终第一页
  querySecle() {
    this.ProductDirectoryBean.pageSize = 10;
    this.ProductDirectoryBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  查询
  queryClick() {
    this.productManageHttpService.catalogManageQueryList(this.ProductDirectoryBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.catalogManageInfoList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { ctlgId: item.prodCatalogId };
        this.productManageHttpService.catalogDelete(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  detailsClick(item) {
    let data;
    data = JSON.stringify(item);
    this.router.navigate(['/pages/tzb/product-manage/product-summary/business-directory-details', { data: data }]);
  }
  modifyClick(item) {
    let data;
    data = JSON.stringify(item);
    this.router.navigate(['/pages/tzb/product-manage/product-summary/business-directory-modify', { data: data }]);
  }
  // 归属部门
  sheMain() {
    this.orgTreeDisplayG = !this.orgTreeDisplayG;
  }
  // 归属部门机构树回调
  treeCallG(param) {
    this.ownerOrgId = '';
    this.belongDepart = '';
    this.orgTreeDisplayG = false;
    if (param.length > 1) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "只可单选，请重新选择！" });
    } else {
      param.forEach(items => {
        this.belongDepart = items.orgName;
        this.ownerOrgId = items.orgId;
      });
    }
    this.ProductDirectoryBean.ownerOrgId = this.ownerOrgId;
    this.departOrStaffPost = this.belongDepart;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.ProductDirectoryBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.ProductDirectoryBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.ProductDirectoryBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.ProductDirectoryBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
