    creat_user: string; // 创建人员
    createdStamp: any;// 创建时间
    CatalogType: any;
    code: any;
    cars = [];
    activeIndex: number = 0;
    //目录
    not_choose_product2: any = [];
    ywList: any = [];
    qyList: any = [];
    defined_id: string = '';
    classify_Name: string;//分类名称
    productCategoryIde: string;

    //提示信息
