    finished() {
        // 目录基本信息
        this.finish = true;
        let cc;
        this.CatalogType.forEach(e => {
            if (this.mulu_type == e.value) {
                cc = e.label;
            }
        });
        const catalogInfo = {
            ctlgId: this.ctlgNumb,
            ctlgName: this.ctlgName,
            prodCatalogTypeId: this.mulu_type,
            prodCatalogType: cc,
            ctlgDsc: this.detailsDescribe,
            catalogOwnerInfo: this.belongDepartlist1,
            catalogSaleInfo: this.saledlist2,
            catalogSalerInfo: this.belonglist,
        };
        // 分类新增信息
        const categoryInfo = [];
        let item1;
        this.isTrouble.forEach(item => {
            if (item.ctlgId !== this.id) {
                item1 = {
                    catalogId: this.id,
                    ctlgId: item.ctlgId,
                    ctlgName: item.ctlgName,
                    parentId: item.parentId,
                    ctlgDsc: item.ctlgDsc,
                    categoryOwnerInfo: item.catalogOwnerInfo,
                    categorySaleInfo: item.categorySaleInfo,
                    categorySalerInfo: item.categorySalerInfo,
                    delFlag: item.delFlag,
                    productCategoryLocalCode: item.productCategoryLocalCode3,
                };
                categoryInfo.push(item1);
            }
        });
        // 产品新增信息
        const param1 = {
            catalogInfo: catalogInfo,
            categoryInfo: categoryInfo,
            productInfo: this.product_add,
        };
        this.productManageHttpService.catalogAndCategoryInfoHandle(param1).subscribe(data1 => {
            if (data1.returnCode.code === 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '业务目录修改成功' });
                const _that = this;
                setTimeout(function () {
                    _that.router.navigate(['/pages/tzb/product-manage/product-summary/product-directory']);
                }, 1000);
            } else {
                this.finish = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data1.errorMessage });
            }
        }, error => {
            this.finish = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
