
import { Component, OnInit, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { EnterpriseDirectoryBean } from './bean/enterprise-directory.bean';
import { TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'

@Component({
  selector: 'app-enterprise-directory',
  templateUrl: './enterprise-directory.component.html',
  styleUrls: ['./enterprise-directory.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 全局目录
 */
export class EnterpriseDirectoryComponent implements OnInit {
  prodName: any;
  prodId: any;
  first: number = 0;//分页控制
  arrangValue: string;//展示层级
  //总页数
  total: string;
  //每页多少条
  pageSize: number = 10;
  //每页多少条
  pageNum: number = 1;
  lineID: string;//暂存线ID
  groupID: string;//暂存组ID
  baseID: string;//暂存基础ID
  //提示信息
  msgs: Message[] = [];
  selectedFiles: TreeNode[];
  //列表
  cars_2: any;
  treeData: any;
  listData: any = {};
  lineValue: any = {};
  groupValue: any = {};

  valueProduct: any = {
    productLineId: '',//产品线ID
    productGroupId: '',//产品组id
    productTemplateId: '',//基础产品id
    pageSize: 10,
    pageNum: 1,
  }
  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;

  //组件的参数
  updateValue: any = [];
  EnterpriseDirectoryBean: EnterpriseDirectoryBean = new EnterpriseDirectoryBean();

  constructor(public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc) { }

  ngOnInit() {
    this.query_line();
    this.baseQuery(this.valueProduct);
  }
  query_line() {
    this.productManageHttpService.productCategoryLineQueryList(this.EnterpriseDirectoryBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        let tempArr = [];
        data.productLineList.forEach(item => {
          let tempObj = { id: 'line' };
          tempObj['label'] = item['productLineName'];
          tempObj['value'] = item;
          tempObj['children'] = [{}];
          tempArr.push(tempObj)
        });
        this.treeData = tempArr;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  //基础查可售
  baseQuery(data) {
    let arr = [];
    // data.statusFlag = ''
    this.productManageHttpService.findAvailableProductList(data).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.resultList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击层级
  nodeCheck(event) {
    if (event.node.id == "line") {
      this.arrangValue = "line";
      this.lineValue = event.node.value;
      this.lineID = event.node.value.productLineId;
      this.cars_2 = [];
      this.pageNum = 1;
      this.pageSize = 10;
      this.valueProduct = {
        productLineId: this.lineID,//产品线ID
        productGroupId: '',//产品组id
        productTemplateId: '',//基础产品id
        pageSize: this.pageSize,
        pageNum: this.pageNum,
      }
      this.first = 0;
      this.baseQuery(this.valueProduct);
      this.listData.prodId = event.node.value.productLineId;
      this.listData.prodName = event.node.value.productLineName;
      this.listData.prodDsc = event.node.value.productLineStatusName;
    }
    if (event.node.id == "group") {
      this.arrangValue = "group";
      this.groupValue = event.node.value;
      this.groupID = event.node.value.groupId;
      this.lineID = event.node.value.productLineId;
      this.cars_2 = [];
      this.pageNum = 1;
      this.pageSize = 10;
      this.valueProduct = {
        productLineId: this.lineID,//产品线ID
        productGroupId: this.groupID,//产品组id
        productTemplateId: '',//基础产品id
        pageSize: this.pageSize,
        pageNum: this.pageNum,
      }
      this.first = 0;
      this.baseQuery(this.valueProduct);
      this.listData.prodId = event.node.value.groupId;
      this.listData.prodName = event.node.value.groupName;
      this.listData.prodDsc = event.node.value.groupDsc;
    }
    if (event.node.id == "jichu") {
      this.cars_2 = [];
      this.arrangValue = "jichu";
      this.baseID = event.node.value.tplId;
      this.groupID = event.node.value.parentId;
      this.lineID = event.node.parent.value.productLineId;
      this.pageNum = 1;
      this.pageSize = 10;
      this.valueProduct = {
        productLineId: this.lineID,//产品线ID
        productGroupId: this.groupID,//产品组id
        productTemplateId: this.baseID,//基础产品id
        pageSize: this.pageSize,
        pageNum: this.pageNum,
      }
      this.first = 0;
      this.baseQuery(this.valueProduct);
      this.listData.prodId = event.node.value.tplId;
      this.listData.prodName = event.node.value.tplName;
      this.listData.prodDsc = event.node.value.tplDsc;
    }
  }
  //  查询
  queryClick() {
    this.valueProduct['productId'] = this.prodId;
    this.valueProduct['productName'] = this.prodName;
    this.baseQuery(this.valueProduct);
  }
  resetClick() {
    this.prodId = '';
    this.prodName = '';
  }
  //+号点击
  nodeExpend(event) {
    if (event.node.id == "line") {
      this.EnterpriseDirectoryBean.productLineId = event.node.value.productLineId;
      let arr = [];
      this.productManageHttpService.productCategoryGroupInfoQueryList(this.EnterpriseDirectoryBean).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.productGroupInfoList.forEach(item => {
            let tempObj = { id: 'group' };
            tempObj['label'] = item['groupName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "group") {
      let param = {
        groupId: event.node.value.groupId,
        statusFlag: 'Y'
      }
      let arr = [];
      this.productManageHttpService.pdGroupTemplateQuery(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.groupTempList.forEach(item => {
            let tempObj = { id: 'jichu' };
            tempObj['label'] = item['tplName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "jichu") {
      let arr = [];
      event.node.children = arr
    }
  }

  // 详情
  basicDetails(item) {
    this.display = true;
    this.headerTitle = '可售产品详情';
    this.showModel = '1';
    this.updateValue = item.productId;
  }
  detailsCall(param) {
    this.display = param;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.first = (currentPage - 1) * this.pageSize;
    this.valueProduct.pageSize = this.pageSize;
    this.valueProduct.pageNum = this.pageNum;
    this.baseQuery(this.valueProduct);
    this.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}





















