import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { BusinessDConfig } from '../bean/business-directory-config.bean';
import { ProductManageHttpService } from '../../../../http/product-manage.http.service';
@Component({
  selector: 'app-directory-config-add',
  templateUrl: './directory-config-add.component.html',
  styleUrls: ['./directory-config-add.component.scss'],
  providers: [ProductManageHttpService]
})

export class DirectoryConfigAddComponent implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //提示信息
    msgs: Message[] = [];
    businessDConfig: BusinessDConfig = new BusinessDConfig();
    select_1: any = [];//默认下拉
    select_2: any = [];//默认下拉
    value_dir: string;
    value_upper: string;

  constructor(
    public productManageHttpService: ProductManageHttpService,
  ) {
  }

  ngOnInit() {
    this.select_1 = [
      { label: '请选择', value: " " },
    ];
    this.query()
  }
  query() {
    this.productManageHttpService.catalogManageQueryList(this.businessDConfig).subscribe(data => {
      if (data.returnCode.code == "success") {
        data.catalogManageInfoList.forEach(item => {
          let obj = {};
          obj['label'] = item.catalogName;
          obj['value'] = item.prodCatalogId;
          this.select_1.push(obj)
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  change() {
    this.businessDConfig.ctlgId = this.value_dir;
    //提交服务
    this.productManageHttpService.ctlgMenuQuery(this.businessDConfig).subscribe(data => {
      if (data.returnCode.code == "success") {
        data.customerMenuList.forEach(item => {
          let obj = {};
          obj['label'] = item.name;
          obj['value'] = item.id;
          this.select_2.push(obj)
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  // 保存
  addSaveBtn() {
    this.businessDConfig.catalogId = this.value_dir;
    this.businessDConfig.parentId = this.value_upper;
    this.productManageHttpService.customerCategoryAdd(this.businessDConfig).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }

}
