import { Component, OnInit, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ProductDirectoryBean } from './../bean/product-directory.bean';
import { TreeNode, Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute, Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';

@Component({
  selector: 'app-business-directory-details',
  templateUrl: './business-directory-details.component.html',
  styleUrls: ['./business-directory-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})


export class BusinessDirectoryDetailsComponent implements OnInit {
  catalogSalerInfo: any;
  catalogSaleInfo: any;
  catalogOwnerInfo: any;
  categoryName: any;
  description: any;
  createUser: any;
  changeUser: any;
  lastUpdatedStamp: string;
  createdStamp: string;
  lastUpdatedTime: string;
  creatTime: string;
  tabMesg: any = tableMessage;//列表无数据
  //总页数
  total: string;
  //每页多少条
  pageSize: number = 10;
  //每页多少条
  pageNum: number = 1;
  head_title: string = '业务目录详情';
  //提示信息
  msgs: Message[] = [];
  //组件的参数
  updateValue: any = [];
  selectedFiles: TreeNode[];
  //列表
  cars_2: any = [];
  treeData: any;
  left_tree: any = [];
  left_tree_arr: any = [];
  hierarchy_arr: any = [];
  ctlgIdValue: any;
  all_tree: any = [];
  first_meu_obj: any = {};//根目录id 组 左侧
  first_meu_left: string = '';//左侧根目录值
  ctlgId: string;
  ctlgName: string;
  ctlgDsc: string;
  prodCatalogType: string;
  belongingDepartment: any = [];//归属部门
  marketingDepartment: any = [];//销售部门
  salesStaffPost: any = [];//销售岗位
  CatalogType: any;
  code: any;
  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;
  //可售分页
  ctlgIdV: string = '';
  maxIdLevelV: number;
  ctlgIdLevelV: number;
  value1: number;
  ctlgOrCategoryLocalCode: string = '';

  ProductDirectoryBean: ProductDirectoryBean = new ProductDirectoryBean();
  constructor(public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc,
    public router: ActivatedRoute,
    public route: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.CatalogType = this.code['CatalogType'];
  }

  ngOnInit() {
    this.router.params.subscribe((item) => {
      let data = JSON.parse(item.data);
      this.ctlgIdV = this.ctlgIdValue;
      this.creatTime = this.commfunc.transDateN2(data.createdStamp);
      this.lastUpdatedTime = this.commfunc.transDateN2(data.lastUpdatedStamp);
      this.ctlgIdValue = data.prodCatalogId;
      this.ctlgId = data.prodCatalogId;
      this.ctlgName = data.catalogName;
      this.changeUser = data.changeUser;
      this.createUser = data.createUser;
      this.ctlgDsc = data.ctlgDsc;
      this.prodCatalogType = data.prodCatalogTypeId;
      let catalogOwner = [], catalogSale = [], catalogSaler = [];
      data.catalogOwnerInfo.forEach(e => {
        catalogOwner.push(e.ownerOrgName);
      });
      this.catalogOwnerInfo = catalogOwner.join(',');
      data.catalogSaleInfo.forEach(e => {
        catalogSale.push(e.saleOrgName);
      });
      this.catalogSaleInfo = catalogSale.join(',');
      data.catalogSalerInfo.forEach(e => {
        catalogSaler.push(e.salerName);
      });
      this.catalogSalerInfo = catalogSaler.join(',');
      this.maxIdLevelV = 2;
      this.ctlgIdLevelV = 0;
      this.value1 = this.ctlgIdLevelV;
    })
    this.query_line();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  query_line() {
    let this_ = this;
    let parm = { ctlgId: this.ctlgIdValue, pageSize: 10000 }
    this.productManageHttpService.ctlgMenuQuery(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.all_tree = data.customerMenuList;
        this.left_tree_arr = data.customerMenuList;
        this.left_tree = this.hierarchyTree(data.customerMenuList, '');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  nodeCheck(event) {
    this.hierarchy_arr = this.getMeuInfo(this.left_tree_arr, event.node.value[0]);
    if (event.node.parent != undefined) {
      this.ctlgIdV = event.node.value[0];
      this.maxIdLevelV = this.hierarchy_arr[1];
      this.ctlgIdLevelV = this.hierarchy_arr[0] - 1;
      // this.ctlgOrCategoryLocalCode = this.ctlgIdV;
      this.categoryName = event.node.label;
      this.pageNum = 1;
      this.productQuery();
    }
    this.value1 = this.ctlgIdLevelV;
  }
  //+号点击
  nodeExpend(event) {
    let this_ = this;
    let parames = {
      ctlgId: event.node.value[0]
    }
    let delData = function (data) {
      event.node.children = this_.hierarchyTree(data.customerMenuList, event.node.value[0]);
    }
    if (event.node.value[1] === 1) {
      this.interfaceComFnc(this.productManageHttpService.ctlgMenuQuery(parames), delData);
    } else {
      this.interfaceComFnc(this.productManageHttpService.ctlgMenuQuery(parames), delData);
    }
  }

  interfaceComFnc(service_method, success_fnc) {
    service_method.subscribe(data => {
      if (data.returnCode != null && data.returnCode.code == "success") {
        if (success_fnc !== null) {
          success_fnc(data);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
  }
  //层级树
  hierarchyTree(menu_list, pid) {
    let menu_arr = [];
    for (let key of menu_list) {
      if (key.pid === pid) {
        menu_arr.push({
          children: [{}],
          label: key.name,
          value: [key.id, key.pid]
        });
      }
    }
    return menu_arr;
  }
  getMeuInfo(tree, curren_pid) {//一级菜单  主目录id
    let count = 1;
    let getFirstId = (tree) => {
      let id = '';
      tree.every(item => {
        item.pid == '' ? id = item.id : null;
      });
      return id;
    }
    //末级菜单id
    let getLastId = (tree) => {
      let id_arr = [];
      tree.forEach(item => {
        let stu = tree.every(item2 => {
          if (item.id != item2.pid) {
            return true;
          }
        }) ? id_arr.push(item.id) : null;
      });
      return id_arr;
    }
    //最大层级深度 
    let getMaxHierarchy = (tree, first_id, last_arr) => {
      let arr = [];
      last_arr.forEach(item => {
        count = 1;
        let max = currentHierarchy(tree, first_id, item)
        arr.push(max);
      });
      return arr.sort()[arr.length - 1];
    }
    //当前层级深度
    //记录层级次数
    function currentHierarchy(tree, first_id, pid) {
      if (pid != first_id) {
        tree.some(item => {
          if (item.id === pid) {
            count++;
            currentHierarchy(tree, first_id, item.pid);
            return false;
          }
        });

      }
      return count;
    }

    let first_id = getFirstId(tree);//一级菜单id
    let last_arr = getLastId(tree);//末级菜单 id组
    let current_hierarchy = currentHierarchy(tree, first_id, curren_pid);//当前层级
    let max_hierarchy = getMaxHierarchy(tree, first_id, last_arr);//最大层级 
    return [current_hierarchy, max_hierarchy]
  }
  // 详情
  basicDetails(item) {
    this.display = true;
    this.headerTitle = '可售产品详情';
    this.showModel = '1';
    this.updateValue = item.productId;
  }
  detailsCall(param) {
    this.display = param;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.productQuery();
    //this.pageNum = 1;
  }
  productQuery() {
    const parames = {
      pageSize: this.pageSize,
      pageNum: this.pageNum,
      ctlgId: this.ctlgIdV,
      maxIdLevel: this.maxIdLevelV.toString,
      ctlgIdLevel: this.ctlgIdLevelV.toString
    };
    this.productManageHttpService.ctlgManageContactAttrInfo(parames).subscribe(data => {
      if (data.returnCode.code == "success") {
        console.log(data);
        this.total = data.total;
        this.ctlgOrCategoryLocalCode = data.productCategoryLocalCode;
        this.cars_2 = data.customerListtemp;
        let catalogOwner = [], catalogSale = [], catalogSaler = [];
        if (data.ownerInfo.length > 0) {
          data.ownerInfo.forEach(e => {
            catalogOwner.push(e.ownerOrgName);
          });
          this.catalogOwnerInfo = catalogOwner.join(',');
        } else {
          this.catalogOwnerInfo = '';
        }
        if (data.saleInfo.length > 0) {
          data.saleInfo.forEach(e => {
            catalogSale.push(e.saleOrgName);
          });
          this.catalogSaleInfo = catalogSale.join(',');
        } else {
          this.catalogSaleInfo = '';
        }
        if (data.saleInfo.length > 0) {
          data.salerInfo.forEach(e => {
            catalogSaler.push(e.salerName);
          });
          this.catalogSalerInfo = catalogSaler.join(',');
        } else {
          this.catalogSalerInfo = '';
        }
        if (this.ctlgIdValue === this.ctlgId) {
          this.creatTime = this.commfunc.transDateN2(data.createdStamp);
          this.lastUpdatedTime = this.commfunc.transDateN2(data.lastUpdatedStamp);
          this.ctlgId = data.prodCatalogId;
          this.ctlgName = data.catalogName;
          this.changeUser = data.changeUser;
          this.createUser = data.createUser;
          this.ctlgDsc = data.ctlgDsc;
          this.description = data.ctlgDsc;
        }
      }
    });
  }

  finished() {
    this.route.navigate(['/pages/tzb/product-manage/product-summary/product-directory']);
  }
}





















