import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EnterpriseDirectoryComponent } from './enterprise-directory/enterprise-directory.component';
import { ProductDirectoryComponent } from './product-directory/product-directory.component';
import { BusinessDirectoryDetailsComponent } from './product-directory/business-directory-details/business-directory-details.component';
import { BusinessDirectoryModifyComponent } from './product-directory/business-directory-modify/business-directory-modify.component';
import { BusinessDirectoryConfigComponent } from './product-directory/business-directory-config/business-directory-config.component';
//页面

const routes: Routes = [
    {
        path: 'enterprise-directory', component: EnterpriseDirectoryComponent,
    },
    {
        path: 'product-directory', component: ProductDirectoryComponent,
    },
    {
        path: 'business-directory-details', component: BusinessDirectoryDetailsComponent,
    },
    {
        path: 'business-directory-modify', component: BusinessDirectoryModifyComponent,
    },
    {
        path: 'business-directory-config', component: BusinessDirectoryConfigComponent,
    },
];

export const ProductSummaryRouting: ModuleWithProviders = RouterModule.forChild(routes);
