  // 修改分类
  modifyCategory(list) {
    if (this.categoryList_add.length > 0) {
      this.categoryList_add.forEach((item, i) => {
        if (item.id === list.id) {
          this.categoryList_add[i].type = 'add';
          this.categoryList_add[i].ctlgName = list.ctlgName;
          this.categoryList_add[i].name = list.name;
          this.categoryList_add[i].ctlgDsc = list.ctlgDsc;
          this.categoryList_add[i].belongingDepartmentList = list.belongingDepartmentList;
          this.categoryList_add[i].marketingDepartmentList = list.marketingDepartmentList;
          this.categoryList_add[i].salesStaffPostList = list.salesStaffPostList;
          this.categoryList_add[i].id = list.id;
          this.categoryList_add[i].catalogId = list.catalogId;
          this.categoryList_add[i].ctlgId = list.ctlgId;
          this.categoryList_add[i].parentId = list.parentId;
          // this.categoryList_add[i].pid = list.pid;
          this.categoryList_add[i].productCategoryLocalCode = list.productCategoryLocalCode;
          this.categoryList_add[i].productCategoryLocalCode3 = list.productCategoryLocalCode3;
          if (list.categoryOwnerInfo) {
            if (list.categoryOwnerInfo.length != 0) {
              this.categoryList_add[i].categoryOwnerInfo = list.categoryOwnerInfo;
            }
          }
          if (list.categorySaleInfo) {
            if (list.categorySaleInfo.length != 0) {
              this.categoryList_add[i].categorySaleInfo = list.categorySaleInfo;
            }
          }
          if (list.categorySalerInfo) {
            if (list.categorySalerInfo.length != 0) {
              this.categoryList_add[i].categorySalerInfo = list.categorySalerInfo;
            }
          }
        }
      });
    }
    this.categoryList_add = _.clone(this.categoryList_add);
    this.classifyName2 = '';
    this.productCategoryLocalCode2 = '';
    this.productCategoryLocalCode3 = '';
    this.cfbelongDepartment2 = [];
    this.belongDepart2 = [];
    this.cfsaleDepartment2 = [];
    this.saled2 = [];
    this.classifyPost2 = [];
    this.classifyDescription2 = "";
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '修改分类成功' });
  }
  //删除
  delete() {
    const param = { ctlgId: this.productCategoryLocalCode2 };
    // 校验当前目录下是否存在分类
    if (param.ctlgId === undefined) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '业务目录未添加分类，删除不成功' });
    } else {
      const list = {
        catalogId: this.first_meu,
        ctlgName: this.classifyName2,
        name: this.classifyName2,
        id: this.productCategoryLocalCode2,
        ctlgId: this.productCategoryLocalCode2,
        parentId: this.parentId,
        pid: this.parentId,
        type: 'delete'
      };
      if (this.categoryList_add.length > 0) {
        this.categoryList_add.forEach((item1, i) => {
          if (item1.id === list.id) {
            // 判断当前分类是否有下挂的分类，如果有，不允许删除
            let isParent = false;
            this.categoryList_add.forEach(item2 => {
              if (list.id === item2.pid) {
                isParent = true;
              }
            });
            if (isParent) {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: '该分类下存在下级分类，不可删除' });
            } else {
              this.categoryList_add.splice(i, 1);
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
              this.classifyName2 = '';
              this.productCategoryLocalCode2 = '';
              this.productCategoryLocalCode3 = '';
              this.cfbelongDepartment2 = [];
              this.belongDepart2 = [];
              this.cfsaleDepartment2 = [];
              this.saled2 = [];
              this.classifyPost2 = [];
              this.classifyDescription2 = "";
              this.query_line();
              this.parentId = undefined;
            }
          }
        });
      }
    }
  }
