import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import { QueryVPListBean } from '../been/business-directory-modify.bean';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { StepsModule, MenuItem, Message } from 'primeng/primeng';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'business-directory-modify-User',
    templateUrl: './business-directory-modify-User.component.html',
    styleUrls: ['./business-directory-modify-User.component.scss']
})

export class BusinessDirectoryModifyUserComponent implements OnInit {
    tabMesg: any = tableMessage;//列表无数据
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    queryVPListBean: QueryVPListBean = new QueryVPListBean();
    param: any;
    lists_1: any = [];
    msgs: Message[] = [];
    department: any = [];
    choose_tr1: any = [];
    pageSize: number = 10;           //			每页显示数量	String(20)
    total: string;
    pageNum: number = 1;           //		当前页	String(20)
    //分页控制
    first: number = 0;
    constructor(
        public productManageHttpService: ProductManageHttpService,
        public route: ActivatedRoute) { }
    ngOnInit() {
        this.first = 0;
    }

    ngOnChanges() {
        this.queryEvent();
    }

    //查询
    queryEvent() {
        this.choose_tr1.forEach(item1 => {
            let choosenum = 0;
            this.department.forEach(items => {
                if (this.lists_1[item1].postId == items.postId) {
                    choosenum++;
                }
            });
            if (choosenum == 0) {
                this.department.push(this.lists_1[item1])
            }
        });
        this.choose_tr1.sort(function (a, b) { return b - a; })
        this.choose_tr1.forEach(item2 => {
            this.lists_1.splice(item2, 1);
        });
        this.lists_1.forEach(iteme => {
            let choosenu = 0;
            let choosen = 0;
            this.department.forEach((items, k) => {
                if (iteme.postId == items.postId) {
                    choosenu++;
                    choosen = k;
                }
            });
            if (choosenu > 0) {
                this.department.splice(choosen, 1)
            }
        });
        this.choose_tr1 = [];
        let param = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            postId: this.queryVPListBean.postId,
            postName: this.queryVPListBean.postName,
        }
        this.productManageHttpService.queryVaguePostList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.lists_1 = data.postList;
                this.total = data.total;
                this.lists_1.forEach((itemse, i) => {
                    this.department.forEach(items => {
                        if (itemse.postId == items.postId) {
                            let nue = i.toString();
                            this.choose_tr1.push(nue);
                        }
                    });
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //查询
    queryclick() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryEvent();
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        //调用查询的方法
        this.queryEvent();
        this.pageNum = 1;
    }

    // 重置
    resetClick() {
        this.queryVPListBean = new QueryVPListBean()
    }

    server() {
        this.queryEvent();
        if (this.inValue == 'one') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart: this.department,
            }
            this.outValue.emit(parmes);
        }
        if (this.inValue == 'two') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart1: this.department,
            }
            this.outValue.emit(parmes);
        }
        if (this.inValue == 'tree') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart2: this.department,
            }
            this.outValue.emit(parmes);
        }
    }

    close() {
        let parm = {
            display: false,
        }
        this.outValue.emit(parm);
    }
}